import React from 'react';
import { NavLink } from 'react-router-dom';

export const Navigation = ({ login }) => {
  return (
    <div className="channel_nav">
      <NavLink to={'/TwitchInfo/channel/' + login} exact className="nav_item">FOLLOWED CHANNELS</NavLink>
      <NavLink to={'/TwitchInfo/channel/' + login + '/clips'} className="nav_item">CLIPS</NavLink>
      <NavLink to={'/TwitchInfo/channel/' + login + '/emotes'} className="nav_item">EMOTES</NavLink>
    </div>
  )
}
